import './AdminProducto.css'

import { observer } from 'mobx-react-lite'
import React, { useEffect } from 'react'
import * as FIcon from 'react-feather'

import {Page, SDataTable, SelectorModal} from '../../components'
import { RUTAS } from '../../constants'
import {DetailViewProps, useToaster} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const numeral = require('numeral')

export const AdminProducto: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const DataTable = React.createRef<any>()
  const Estado = history.location.pathname.split('/')[3]
  const addToast = useToaster()

  React.useEffect(() => {
    DataTable.current.refresh()
  }, [window.location.pathname, window.location.search])

  const setup = async () => {
    obtenerFabricantes()
  }

  useEffect(() => {
    setup()
  }, [])

  const visualizar = (row: any) => {
    history.push(RUTAS.ADMIN_PRODUCTO_DETAIL(Estado, row.id))
  }

  const editar = (row: any) => {
    history.push(RUTAS.ADMIN_PRODUCTO_UPDATE(Estado, row.id))
  }

  const eliminar = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    const dtable = DataTable.current
    rootStore.environment.api.eliminarProducto(row.id).then((result: any) => {
      if (result.kind === 'ok') {
        addToast('Eliminado satisfactoriamente', 'success')
        dtable.refresh()
      } else {
        addToast('Ocurrió un error al eliminar vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const obtenerFabricantes = () => {
    // @ts-ignore
    rootStore.environment.api.fabricantesList().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setFabricantes(result.data)
      }
    })
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}>
          <FIcon.Eye size={14} />
        </span>
        <span className={'button ml-3'} title={'Editar'} onClick={() => editar(row)}>
          <FIcon.Edit size={14} />
        </span>
        <span className={'button ml-3'} title={'Eliminar'} onClick={() => eliminar(row)}>
          <FIcon.Trash size={14} />
        </span>
      </div>
    )
  }

  const c = (new URLSearchParams(window.location.search)).get('c')
  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Productos</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Productos</li>
              </ol>
            </div>
          </div>
          <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
            <li className="nav-item">
              <SelectorModal
                titulo={'Categoría'}
                titulo_label={'Seleccioná la categoría'}
                listado_elementos={rootStore.categorias}
                // @ts-ignore
                listado_elementos_seleccionados={c ? parseInt(c) : []}
                datos_filtrados={(data: any) => {
                  let currentUrlParams = new URLSearchParams(window.location.search)
                  if (data.length) {
                    currentUrlParams.set('c', data[0])
                  } else {
                    currentUrlParams.delete('c')
                  }
                  history.push(window.location.pathname + '?' + currentUrlParams.toString())
                }}
                multiple={false}
                llave_label_opcion={'nombre'}
              />
            </li>
            <li className="nav-item ml-auto">
              <button
                className="btn btn-primary d-flex align-items-center px-3"
                id="add-productos"
                onClick={() => {
                  history.push(RUTAS.ADMIN_PRODUCTO_CREATE(Estado))
                }}
              >
                Crear Producto
              </button>
            </li>
          </ul>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  let currentUrlParams = new URLSearchParams(window.location.search)
                  let moderado = match.params.estado !== 'pendiente'
                  let c = currentUrlParams.get('c') // categoria
                  return rootStore.environment.api.productos(page, perPage, order, search, moderado, {c})
                }}
                columns={[
                  {
                    name: 'ID',
                    selector: 'id',
                    sortable: true,
                  },
                  {
                    name: 'Fabricante',
                    selector: 'fabricante',
                    sortable: true,
                    omit: !loginStore.administrador,
                    cell: (row: any) => (row.fabricante ? row.fabricante.nombre : ''),
                  },
                  {
                    name: 'Nombre',
                    selector: 'nombre',
                    sortable: true,
                  },
                  {
                    name: 'Categoría',
                    selector: 'categoria',
                    sortable: true,
                    cell: (row: any) => (typeof row.categoria === 'string' ? row.categoria : row.categoria.nombre),
                  },
                  {
                    name: 'Precio',
                    selector: 'precio',
                    sortable: true,
                    cell: (row: any) => numeral(row.precio).format('0,0'),
                  },
                  {
                    name: 'Cantidad disponible',
                    selector: 'cantidad_disponible',
                    sortable: true,
                    cell: (row: any) => numeral(row.cantidad_disponible).format('0,0'),
                  },
                  {
                    name: 'Aprobado',
                    selector: 'activo',
                    sortable: true,
                    cell: (row: any) => (row.activo ? 'Sí' : 'No'),
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminProducto
